.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 6px;
  height: 6px;
  background: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(150%, 150%);
}

.loader:before {
  top: auto;
  bottom: 0;
  left: auto;
  right: 0;
  transform: translate(-150%, -150%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none;
}

.cat-info {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.cat-image {
  width: 300px;
  margin-right: 20px;
}

.cat-text {
  flex: 1;
}

